export class BlockFactory {
    createBlock(id, type, content = '', metadata = {}) {
        const now = new Date();
        return {
            id,
            type,
            content,
            metadata: this.getDefaultMetadata(type, metadata),
            createdAt: now,
            updatedAt: now
        };
    }
    getDefaultMetadata(type, metadata) {
        const defaults = {
            'paragraph': {},
            'heading-1': { level: 1 },
            'heading-2': { level: 2 },
            'heading-3': { level: 3 },
            'heading-4': { level: 4 },
            'heading-5': { level: 5 },
            'heading-6': { level: 6 },
            'quote': {},
            'code': { language: 'text' },
            'bullet-list': { level: 0 },
            'numbered-list': { level: 0, number: 1 },
            'checklist': { checked: false, level: 0 },
            'horizontal-rule': {},
            'image': { src: '', alt: '', width: 600, caption: '' }
        };
        return { ...defaults[type], ...metadata };
    }
    static isTextBlock(type) {
        return [
            'paragraph',
            'heading-1',
            'heading-2',
            'heading-3',
            'heading-4',
            'heading-5',
            'heading-6',
            'quote',
            'code'
        ].includes(type);
    }
    static isListBlock(type) {
        return ['bullet-list', 'numbered-list', 'checklist'].includes(type);
    }
    static isHeadingBlock(type) {
        return type.startsWith('heading-');
    }
    static getHeadingLevel(type) {
        if (!this.isHeadingBlock(type))
            return 0;
        return parseInt(type.split('-')[1]) || 1;
    }
}
